import { createRouter, createWebHistory } from 'vue-router'
import Login from '../components/Login.vue'
import Dashboard from '../views/Dashboard.vue'
import Campaign from '@/views/Campaign.vue'
import CampaignPreview from '@/views/CampaignPreview.vue'
import CreateCampaign from '../views/CreateCampaign.vue'
import Device from '../views/Device.vue'
import DevicePreview from '../views/DevicePreview.vue'
import AddDevice from '../views/AddDevice.vue'
import Profile from '@/views/Profile.vue'

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    meta: { isLogin: true },
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/campaign',
    name: 'Campaign',
    component: Campaign,
    // children: [
    //   {
    //     path: 'campaign/campaignpreview',
    //     name: 'CampaignPreview',
    //     component: CampaignPreview,
    //   },
    // ],
  },
  {
    path: '/campaignpreview',
    name: 'CampaignPreview',
    component: CampaignPreview,
  },
  {
    path: '/createcampaign',
    name: 'CreateCampaign',
    component: CreateCampaign,
  },
  {
    path: '/device',
    name: 'Device',
    component: Device
  },
  {
    path: '/devicePreview',
    name: 'DevicePreview',
    component: DevicePreview,
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
  },
  {
    path: '/add-device',
    name: 'AddDevice',
    component: AddDevice,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
