<script>
import CreateTargetDevices from '@/components/CreateTargetDevices.vue';

export default {
    name: 'CreateCampaign-',
    components: {
        CreateTargetDevices
    },
    data() {
        return {
            uploadedFiles: [],
        };
    },
    methods: {
        handleFileChange() {
            const input = this.$refs.fileInput;
            const files = input.files;

            // Process each selected file
            for (let i = 0; i < files.length; i++) {
                const file = files[i];
                const reader = new FileReader();

                reader.onload = (e) => {
                    // Create a thumbnail URL for the image
                    const thumbnail = e.target.result;

                    // Add the file and its thumbnail to the list
                    this.uploadedFiles.push({
                        file,
                        thumbnail,
                    });

                    // Clear the input value to allow selecting the same file again
                    input.value = '';

                    // Handle file selection here
                    console.log(`Selected file: ${file.name}`);
                    // You can perform further actions with the selected file here
                };

                // Read the file as a data URL (for images)
                reader.readAsDataURL(file);
            }
        },
        removeFile(index) {
            // Remove the file and its thumbnail from the list
            this.uploadedFiles.splice(index, 1);
        },

        selectFile() {
            // Trigger the file input when either button is clicked
            this.$refs.fileInput.click();
        },

    },
}
</script>

